.waved-heading {
  font-size: 34px;
  font-weight: bold;
  letter-spacing: -0.025em;
}
.waved-heading .waved {
  padding-left: 3px;
  color: #ffffff;
}
.waved-heading .waved > span {
  position: relative;
  display: inline-block;
}
.waved-heading .waved > span > span {
  position: relative;
  z-index: 2;
}
.waved-heading .waved > span::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 150%;
  height: 100%;
  border-radius: 50%;
  translate: -50% -50%;
  -webkit-transform: skew(-20deg);
  transform: skew(-20deg);
  background: #ed2939;
}

.subtitle {
  padding-bottom: 0.5rem;
  font-weight: bold;
  font-size: 18px;
  font-size: 16px;
}

.more-button {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
  align-items: center;
}
.more-button a {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 70%;
  background: transparent;
  color: #27412B;
  border: 1.5px solid #27412B;
  padding: 10px 32px;
  border-radius: 32px;
  outline: 0;
  font-size: 19px;
  font-weight: 500;
  line-height: 24px;
  text-decoration: none;
  transition: all 0.2s;
}
.more-button a:hover {
  background: #ffb652;
  border-color: #ffb652;
  color: #fff;
}
@media (max-width: 767px) {
  .more-button a {
    width: 80%;
  }
}
@media (max-width: 575px) {
  .more-button a {
    width: 90%;
  }
}

/* Page Title Header Css */
.page-title-header {
  color: #1A213D;
}
.page-title-header .page-title-category {
  font-size: 15px;
}
.page-title-header .page-title_wrapper .page-title {
  font-size: 35px;
  font-weight: bold;
}

/* Media Container Css */
.media-container {
  margin-bottom: 20px;
}
.media-container .media-items-wrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  gap: 8px;
}
.media-container .media-items-wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.media-container .media-items-wrapper .media-item:nth-child(1) {
  grid-column: span 2/span 2;
  grid-row: span 4/span 4;
}
.media-container .media-items-wrapper .media-item:nth-child(1) img {
  border-radius: 1rem 0px 0px 1rem;
}
.media-container .media-items-wrapper .media-item:nth-child(2) {
  grid-column-start: 3;
  grid-row: span 2/span 2;
}
.media-container .media-items-wrapper .media-item:nth-child(3) {
  grid-column-start: 4;
  grid-row: span 2/span 2;
}
.media-container .media-items-wrapper .media-item:nth-child(3) img {
  border-top-right-radius: 1rem;
}
.media-container .media-items-wrapper .media-item:nth-child(4) {
  grid-column-start: 3;
  grid-row-start: 3;
  grid-row: span 2/span 2;
}
.media-container .media-items-wrapper .media-item:nth-child(5) {
  grid-column-start: 4;
  grid-row-start: 3;
  grid-row: span 2/span 2;
}
.media-container .media-items-wrapper .media-item:nth-child(5) img {
  border-bottom-right-radius: 1rem;
}

/* Product Promo Section */
.product-promo .promo-label {
  display: flex;
  border: 1px solid #0039a6;
  border-radius: 4px;
  color: #0039a6;
  font-size: 16px;
  font-weight: 600;
  padding: 10px 16px;
  max-width: 500px;
  column-gap: 8px;
  line-height: 24px;
  letter-spacing: 0px;
}
