/* Booking Assistant Section */
.booking-assistant {
  margin-top: 12px;
}
.booking-assistant .tourDetailsSelection {
  width: 95%;
}
.booking-assistant .tourDetailsSelection .timeListStrip {
  display: flex;
  flex-direction: row;
  align-items: center;
