.flatpickr-calendar .flatpickr-innerContainer .flatpickr-days .flatpickr-day.selected.startRange + .endRange:not(:nth-child(7n+1)), .flatpickr-calendar .flatpickr-innerContainer .flatpickr-days .flatpickr-day.startRange.startRange + .endRange:not(:nth-child(7n+1)), .flatpickr-calendar .flatpickr-innerContainer .flatpickr-days .flatpickr-day.endRange.startRange + .endRange:not(:nth-child(7n+1)) {
